    //     // 获得该容器到窗口最左的距离
    //     var offsetLeft = graph.offset().left;    

    //     var canvas = document.createElement("canvas");
    //     // 将画布宽&&高放大两倍，目的在于保存更高清的图片
    //     canvas.width = w * 2;    
    //     canvas.height = h * 2;
    //     var context = canvas.getContext("2d");
    //     var scale = 2;
    //     context.scale(2, 2);
    //     context.translate(-offsetLeft, -offsetTop);

    //     // const pdf = new jsPDF("l", "pt", [640, 640]);
    //     // const pdfCanvas = document.createElement("canvas");
    //     let node = graph
    //     let backgroundColor = node.style["backgroundColor"]
    //     // console.log("🚀 ~ file: index.js ~ line 231 ~ Playground ~ newPromise ~ backgroundColor", backgroundColor)
    //     let backgroundImage = node.style["background"].split(" ")[0]
    //     // console.log("🚀 ~ file: index.js ~ line 233 ~ Playground ~ newPromise ~ backgroundImage", backgroundImage)
    //     let opts = {
    //       scale: scale,
    //       canvas: canvas,
    //       width: w,
    //       height: h,
    //       useCORS: true,
    //       background: backgroundImage
    //     }

    //   html2canvas(document.getElementById('demo-chart'),opts).then(canvas => {
    //        // 得到canvas画布的单位是px 像素单位
    //     var contentWidth = canvas.width
    //     console.log("🚀 ~ file: index.js ~ line 244 ~ Playground ~ html2canvas ~ contentWidth", contentWidth)
    //     var contentHeight = canvas.height
    //     console.log("🚀 ~ file: index.js ~ line 246 ~ Playground ~ html2canvas ~ contentHeight", contentHeight)

    //     // 将canvas转为base64图片
    //     var pageData = canvas.toDataURL('image/jpeg', 1.0)

    //     // // 设置pdf的尺寸，pdf要使用pt单位 已知 1pt/1px = 0.75   pt = (px/scale)* 0.75,2为上面的scale 缩放了2倍
    //     // var pdfX = (contentWidth + 10) / 2 * 0.75
    //     // console.log("🚀 ~ file: index.js ~ line 252 ~ Playground ~ html2canvas ~ pdfX", pdfX)
    //     // var pdfY = (contentHeight + 500) / 2 * 0.75 // 500为底部留白
    //     // console.log("🚀 ~ file: index.js ~ line 254 ~ Playground ~ html2canvas ~ pdfY", pdfY)

    //     // 设置内容图片的尺寸，img是pt单位
    //     // var imgX = pdfX;
    //     // var imgY = (contentHeight / 2 * 0.75); //内容图片这里不需要留白的距离

    //     // 初始化jspdf 第一个参数方向：默认''时为纵向，第二个参数设置pdf内容图片使用的长度单位为pt，第三个参数为PDF的大小，单位是pt
    //     var PDF = new jsPDF('', 'pt', [640, 640])

    //     // 将内容图片添加到pdf中，因为内容宽高和pdf宽高一样，就只需要一页，位置就是 0,0
